/**
 * 
 * @param {Object} answers -  { questionId: score } (score: 1-7)
 * @param {Array} items - 
 * @returns {Object} { anxiety: number, avoidance: number }
 */
export const calculateDimensionScore = (answers, items) => {
  let anxietySum = 0;
  let anxietyCount = 0;
  let avoidanceSum = 0;
  let avoidanceCount = 0;

  items.forEach((item) => {
    const rawScore = answers[item.id];
    if (rawScore === undefined) return; // 

    //  (1-78 - score)
    const finalScore = item.reverse ? 8 - rawScore : rawScore;

    if (item.dimension === "anxiety") {
      anxietySum += finalScore;
      anxietyCount++;
    } else if (item.dimension === "avoidance") {
      avoidanceSum += finalScore;
      avoidanceCount++;
    }
  });

  return {
    anxiety: anxietyCount > 0 ? (anxietySum / anxietyCount).toFixed(2) : 0,
    avoidance:
      avoidanceCount > 0 ? (avoidanceSum / avoidanceCount).toFixed(2) : 0,
  };
};

/**
 * 
 * @param {number} anxiety - 
 * @param {number} avoidance - 
 * @returns {string}  (Secure, Anxious, Avoidant, Disorganized)
 */
export const getAttachmentStyle = (anxiety, avoidance) => {
  //  4.0 (1-7)
  //  3.5
  const THRESHOLD = 4.0;

  const highAnxiety = anxiety > THRESHOLD;
  const highAvoidance = avoidance > THRESHOLD;

  if (!highAnxiety && !highAvoidance) return "Secure ()";
  if (highAnxiety && !highAvoidance) return "Anxious ()";
  if (!highAnxiety && highAvoidance) return "Avoidant ()";
  if (highAnxiety && highAvoidance) return "Disorganized ()";

  return "Unknown";
};
